.project__head{
  display: flex;
  justify-content: space-between;
  margin-bottom: 77px;
}

.project__name{
  font-weight: 500;
  margin-bottom: 10px;
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
  
}
.projtct__catgories{
  width: 318px;
}

.project__category{
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  color: #0005A5
}

.project__head-item{
  margin-bottom: 20px;
}

.inner .project__title{
  max-width: 1000px;
  width: calc(100% - 400px);
  padding-right: 20px;
  line-height: 40px;
  margin-bottom: 0px;
}

.project__pic {
  margin-bottom: 50px;
}

.project__about{
  display: flex;
  justify-content: space-between;
  margin-bottom: 78px;
}

.project__description{
  
  padding-right: 10px;
}

.project__about-left{
  width: calc(55% - 60px);
}
.project__about-right{
  width: calc(45% - 60px);
}

.project__description-text{
  height: 414px;
  overflow: hidden;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  p{
    margin-bottom: 30px;
  }
  &.active{
    height: auto;
  }
}

.link-all{
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  color: #005BDE;
  display: inline-block;
  cursor: pointer;
  text-decoration: underline;
  border: none;
}

.project__description-alltext{
  padding-top: 15px;
}

.project__pasport{
  .ico-link{
    display: flex;
    align-items: flex-start;
    margin-bottom: 60px;
    width: calc(100% - 50px);
    &:last-child{
      margin-bottom: 0px;
    }
    svg{
      margin-right: 9px;
    }
  }
}

.ico-link-pic{
  width: 50px;
}

.partic{
  display: flex;
  padding-bottom: 70px;
  border-bottom: 1px solid #C5C5C5;
  margin-bottom: 70px;
}

.partic__photo {
  width: 350px;
  height: 350px;
  border-radius: 50%;
  margin-right: 116px;
}

.partic__info{
  flex-grow: 1;
}

.partic__name{
  margin-bottom: 34px;
}

.partic__block{
  margin-bottom: 20px;
  p{
    font-weight: 600;
    font-size: 18px;
    line-height: 20px;
    &:first-child{
      font-weight: 500;
      color: #A2A6AB;
      margin-bottom: 5px;
    }
    &:last-child{
      font-weight: bold;
      color: #000;
    }
  }
}

.project__video{
  .protection__video{
    padding-top: 0px;
  }
}

.partic__blocks.active{
  .partic__block:nth-child(4) ~ .partic__block{
    display: block;
  }
}


.score{
  padding-top: 100px;
  &:last-child{
    margin-bottom: 0px;
  }
}

.score__table{
  margin-bottom: 30px;
}

.score__head{
  display: flex;
  justify-content: space-between;
  background-color: #F7F7FA;
  .score__number{
    height: 51px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 900;
    font-size: 18px;
    line-height: 18px;
    color: #005BDE;
    width: 100%;
  }
  .score__text{
    height: 51px;
    display: flex;
    align-items: center;
    padding-left: 8px;
    padding-right: 8px;
    font-weight: 900;
    font-size: 16px;
    line-height: 18px;
    color: #0005A5;
  }
}

.score__tr{
  display: flex;
}

.score__item{
  flex: 0 65px;
}

.score__text{
  flex-grow: 1;
  font-weight: 500;
}

.-point-10 .score__text{
  width: calc(100% - 650px);
}

.-point-5 .score__text{
  width: calc(100% - 325px);
}

.score__point{
  height: 61px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-left: 1px solid #D1CFD7;
  &:hover span{
    border-color: #005BDE;
  }
  .score__number{
    display: none;
  }
  input{
    display: none;
  }
  input:checked + span{
    background-color: #005BDE;
    border-color: #005BDE;
  }
  span{
    content: "";
    position: relative;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 2px solid #A2A6AB;
  }
}


.score__body{
  border: 1px solid #D1CFD7;
}

.score__tr:nth-child(2n){
  background-color: #F7F7FA;
}
.score__body .score__text{
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 8px;
  padding-right: 8px;
}

.btn-right{
  display: flex;
  justify-content: flex-end;
}

.block-404{
  p{
    font-weight: 800;
    font-size: 220px;
    line-height: 266px;
    color: #fff;
    text-align: center;
  }
  span{
    text-transform: uppercase;
    text-align: center;
    display: block;
    max-width: 728px;
    margin: 0 auto;
    font-weight: 800;
    font-size: 40px;
    line-height: 48px;
    color: #fff;
  }
}


@media(max-width: 1200px){
  .project__description{
    margin-bottom: 50px;
  }
  .project__head{
    flex-wrap: wrap;
  }
  .inner .project__title{
    width: 100%;
    max-width: 100%;
  }
  .projtct__catgories{
    order: -1;
    margin-bottom: 20px;
  }
}

@media(max-width: 980px){
  .project__about{
    display: block;
  }
  .project__description-text{
    font-size: 13px;
    line-height: 20px;
  }
  .project__about-left{
    width: 100%;
    margin-bottom: 40px;
  }
  .project__about-right{
    width: 100%;
    margin-bottom: 40px;
  }
  .partic__block p{
    font-size: 13px;
    line-height: 20px;
  }
  .project__pasport .ico-link{
    margin-bottom: 30px;
  }
  .link-all{
    font-size: 16px;
  }
  .project__name{
    margin-bottom: 0px;
  }
  .project__head{
    margin-bottom: 20px;
  }
  .inner .project__title{
    line-height: 30px;
    font-size: 18px;
  }
  .project__file{
    width: 100%;
    margin-bottom: 18px;
  }
  .score{
    padding-top: 50px;
  }
  .score__point{
    border: none;
  }
  .score__head{
    display: none;
  }
  .score__body{
    border: none;
    width: calc(100% + 40px);
    margin-left: -20px;
  }
  .score__body .score__text{
    padding: 0px;
  }
  .score__tr{
    flex-wrap: wrap;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 24px;
    padding-bottom: 32px;
  }
  .score__text{
    width: 100%;
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 20px;
    .-point-10 &{
      width: 100%;
    }
    .-point-5 &{
      width: 100%;
    }
  }
  
  .-point-10 .score__item{
    flex: 0 10%;
  }
  .-point-5 .score__item{
    flex: 0 10%;
  }

  .score__item{
    height: auto;
  }
  

  .score__item  .score__number{
    display: block;
    width: 100%;
    text-align: center;
    font-weight: 800;
    font-size: 14px;
    line-height: 20px;
    color: #005BDE;
    padding-top: 10px;
  }
  .score__point span{
    width: 16px;
    height: 16px;
  }
  .score__point{
    flex-wrap: wrap;
    height: auto;
  }
  .btn-right{
    justify-content: center;
  }

}


@media(max-width: 768px){
  .block-404{
    padding-top: 40px;
    p{
      font-weight: 800;
      font-size: 120px;
      line-height: 145px;
    }
    span{
      font-weight: 800;
      font-size: 22px;
      line-height: 30px;
      max-width: 320px;
    }
  }
  .project__name,
  .project__category{
    font-size: 18px;
  }
  .inner .project__title{
    text-align: left;
  }
  .project__organiztion-title{
    font-size: 16px;
  }
  .project__organiztion-name{
    font-size: 16px;
    line-height: 20px;
  }
  .project__organiztion{
    margin-bottom: 48px;
  }
  .project__description-text{
    font-size: 13px;
    line-height: 20px;
    height: auto;
  }
  .project__description-alltext{
    display: none;
  }
  .project__pasport .ico-link{
    margin-bottom: 30px;
  }
  .partic{
    display: block;
    padding-bottom: 40px;
    margin-bottom: 40px;
  }
  .partic__photo{
    width: 240px;
    height: 240px;
    border-radius: 50%;
    margin: 0 auto 18px;
  }
  .partic__info {
    width: 100%;
  }
  .partic__block{
    margin-bottom: 25px;
    p:first-child,
    p:last-child{
      font-size: 13px;
      line-height: 20px;
    }
  }
  .partic__block:nth-child(4) ~ .partic__block{
    display: block;
  }
  .partic__all.link-all{
    display: none;
  }
  .project__file p{
    font-size: 16px;
    line-height: 20px;
  }
  .project__file a{
    font-size: 20px;
    line-height: 24px;
    padding-top: 10px;
    padding-bottom: 10px;
    justify-content: center;
  }
}