.main{
  padding-top: 150px;
  background: url(../img/main__bg.jpg) no-repeat center / cover;
  padding-bottom: 154px;
  text-align: center;
  color: #fff;
}

.main__logo{
  position: absolute;
  left: 20px;
  top: 0;
  transform: translateY(-100px);
}

.main__name{
  font-weight: bold;
  font-size: 24px;
  line-height: 33px;
  margin-bottom: 20px;
}

.main__content-logo {
  margin-bottom: 25px;
  display: flex;
  justify-content: center;
}

.main__title {
  font-weight: 900;
  font-size: 38px;
  line-height: 52px;
  margin-bottom: 43px;
}
.main__text{
  max-width: 835px;
  margin: 0 auto;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #0005A5;
}


.about {
  padding-bottom: 100px;
  z-index: 100;
  position: relative;
}

.about__white{
  background: #FFFFFF;
  border-radius: 20px;
  padding: 46px 78px;
  margin-top: -110px;
  width: 100%;
}

.about__text{
  width: calc(100% - 334px);
  padding-right: 10px;
  p{
    font-weight: 600;
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 40px;
    max-width: 700px;
    height: 160px;
    overflow: hidden;
  }
}

.text-all{
  font-weight: bold;
  font-size: 16px;
  line-height: 30px;
  text-decoration: underline;
  color: #0005A5;
  &:hover{
    text-decoration: none;
  }
}

.about__wrap{
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}

.about__right{
  width: 334px;
}

.about__btn{
  margin-bottom: 50px;
}

.abour__link{
  display: flex;
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
  color: #0005A5;
  margin-bottom: 22px;
  svg{
    margin-right: 12px;
  }
}
.nomination{
  margin-bottom: 110px;
}
.nomination__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  border-radius: 7px;
  overflow: hidden;
  margin-bottom: 62px;
  &:after{
    content: "";
    position: absolute;
    left: 0;
    height: 100%;
    width: 12px;
    background: linear-gradient(180deg, #0005A5 0%, #00ACFF 100%);
  }
}

.nomination__title{
  margin-left: 85px;
  max-width: 321px;
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  color: #0005A5;
}

.nomination__text{
  font-size: 17px;
  line-height: 23px;
  width: calc(100% - 480px);
}


.timer{
  margin-bottom: 134px;
}

.timer__wrap{
  display: flex;
  justify-content: space-between;
  padding-left: 85px;
  padding-top: 100px;
  .btn-center{
    align-self: start;
  }
}

.timer__description{
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  color: #0005A5;
  max-width: 280px;
  text-transform: uppercase;
}


.timer__block{
  display: flex;
  justify-content: center;
}

.timer__number{
  font-weight: 800;
  font-size: 60px;
  line-height: 30px;
  color: #005BDE;
  margin-bottom: 21px;
}

.timer__item--center{
  padding-left: 75px;
  padding-right: 75px;
  .timer__number{
    &:after, 
    &:before{
      content: ":";
      position: absolute;
      top: -5px;
    }
    &:after{
      left: 100%;
      margin-left: 30px;
    }
    &:before{
      right: 100%;
      margin-right: 30px;
    }
  }
}

.timer__text{
  font-weight: 800;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
}

.calendar {
  padding-top: 75px;
  padding-bottom: 100px;
}

.caleandar__list{
  display: flex;
  margin-right: -30px;
  margin-bottom: 30px;
  padding-top: 45px;
}


.calendar__item {
  position: relative;
  width: calc(25% - 30px);
  margin-right: 30px;
}

.calendar__pic{
  display: flex;
  justify-content: center;
  margin-bottom: 17px;
  min-height: 240px;
}

.calendar__text{
  font-weight: 800;
  font-size: 26px;
  line-height: 45px;
  display: inline-block;
  color: #0005A5;
  padding-bottom: 18px;
  margin-bottom: 25px;
  &:after{
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 12px;
    background: linear-gradient(90deg, #0005A5 0%, #00ACFF 100%);
    border-radius: 0px 0px 6px 6px;
  }
}

.calendar__date{
  font-weight: 300;
  font-size: 40px;
  line-height: 45px;
  text-transform: uppercase;
  color: #0005A5;
}



.organizations {
  padding: 65px 0 112px;
  background: url(../img/footer.jpg) no-repeat top center/ cover;
}

.organizations__panels{
  display: flex;
  justify-content: center;
  margin-bottom: 76px;
}

.organizations__panel{
  width: 100%;
  max-width: 320px;
  margin: 0 14px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 180px;
  border-radius: 16px;
  &:first-child{
    margin-left: 0px;
  }
  &:last-child{
    margin-right: 0px;
  }
}

.footer__wrap{
  display: flex;
  justify-content: space-between;
  color: #fff;
  font-size: 14px;
}

.copyright{
  max-width: 298px;
}

.contacts{
  a{
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 3px;
    display: block;
    color: #fff;
    &:last-child{
      margin-bottom: 0px;
    }
  }
}

.socilas {
  display: flex;
  justify-content: center;
}

.social{
  margin-right: 20px;
  margin-left: 20px;
  &:first-child{
    margin-left: 0px;
  }
  &:last-child{
    margin-right: 0px;
  }
}

.political{
  max-width: 240px;
  a{
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #fff;
    text-decoration: underline;
  }
}

.footer__lk{
  text-transform: uppercase;
}


.footer-main{
  display: flex;
  justify-content: space-between;
  h2{
    margin-bottom: 30px;
  }
  .contacts{
    margin-bottom: 25px;
  }
  .political{
    margin-bottom: 30px;
  }
  .footer__lk{
    margin-bottom: 30px;
    text-align: right;
  }
  .nav{
    margin-right: 0px;
  }
  .nav__link {
    text-align: right;
    width: 100%;
    margin-bottom: 20px;
    display: block;
    justify-content: space-between;
  }
  .socilas {
    justify-content: space-between;
    max-width: 283px;
  }
  .social{
    margin-left: 0px;
    margin-right: 0px;
  }
}

.footer__left{
  width: 100%;
  max-width: 666px;
}

.footer__right{
  width: calc(100% - 750px);
  display: flex;
  justify-content: space-between;
}

.footer__right-start{
  width: calc(50% - 10px);
  padding-top: 60px;
}

.footer__right-end{
  width: calc(50% - 10px);
}

.footer__lk a{
  font-weight: 800;
  font-size: 20px;
  line-height: 20px;
  color: #fff;
  text-decoration: underline;
}

.mobile-pic {
  display: none;
  img{
    width: 100%;
  }
}




@media(max-width: 1300px){
  .main__text p{
    max-width: 460px;
  }
  .organizations__panels{
    display: block;
  }
  .organizations__panel{
    margin-bottom: 20px;
  }
  .organizations__panel:last-child{
    margin-left: 0px;
  }
  .footer__left{
    max-width: 500px;
  }
  .footer__right{
    width: calc(100% - 525px);
  }
  .footer__right-end{
    width: 200px;
  }
  .footer__right-start{
    padding-top: 95px;
    width: calc(100% - 200px);
    .political{
      width: 1000%;
      text-align: left;  
    }
  }
  
} // 1300

@media(max-width: 1100px){
  .main__logo{
    position: relative;
    display: flex;
    justify-content: center;
  }
  .timer__wrap{
    padding-left: 0px;
  }
}

@media(max-width: 980px){
  .footer__left {
    max-width: 332px;
}
  .footer__right{
    width: calc(100% - 400px);
  }
  .footer__right-start{
    padding-top: 59px;
  }
  .main{
    padding-top: 21px;
    padding-bottom: 200px;
  }
  .main__logo{
    max-width: 80px;
    margin: 0 auto 10px;
  }
  .main__name{
    font-weight: 900;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    margin-bottom: 7px;
  }
  .main__content-logo{
    margin-bottom: 10px;
    img{
      max-width: 290px;
    }
  }
  .main__title{
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 31px;
  }
  .main__text{
    font-size: 14px;
    line-height: 20px;
    color: #fff;
  }

  .h2-big{
    font-weight: bold;
    font-size: 30px;
    line-height: 41px;
    color: #0005A5;
    margin-bottom: 35px;
    text-align: center;
  }

  .about__wrap{
    display: block;
  }

  .about__text{
    font-size: 14px;
    line-height: 20px;
    width: 100%;
    p{
      height: auto;
    }
  }
  .text-all{
    display: none;
  }
  .about__btn{
    span{
      display: none;
    }
  }
  .about{
    background-color: #fff;
  }
  .about__white{
    padding: 0;
    padding-top: 25px;
  }
  .nomination__item{
    display: block;
    overflow: inherit;
    margin-bottom: 30px;
  }
  .nomination__item:after{
    display: none;
  }
  .nomination{
    margin-bottom: 52px;
  }
  .mobile-pic{
    display: block;
  }
  .nomination__title{
    position: relative;
    font-size: 16px;
    line-height: 20px;
    padding-left: 20px;
    margin-left: -20px;
    text-transform: uppercase;
    margin-bottom: 11px;
    &:after{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 12px;
      background: linear-gradient(180deg, #0005A5 0%, #00ACFF 100%);
    }
  }
  .nomination__text{
    width: 100%;
    font-size: 13px;
    line-height: 20px;
  }
  .timer{
    padding-top: 33px;
    padding-bottom: 39px;
    margin-bottom: 40px;
    h2.h2-big{
      display: none;
    }
  }
  .timer__wrap{
    padding-top: 0px;
    display: block;
  }
  .timer__description {
    text-align: center;
    font-weight: bold;
    font-size: 30px;
    line-height: 41px;
    margin: 0 auto 40px;
    max-width: 350px;
    
  }
  .timer__panel{
    padding: 0;
    background-color: transparent;
    margin-bottom: 86px;
  }
  .timer__panel .h2{
    color: #fff;
  }
  .timer__block{
    background-color: #fff;
    padding-top: 0px;
    padding-bottom: 30px;
    border-radius: 16px;
    max-width: 450px;
    margin: 0 auto;
  }
  .timer__number{
    font-size: 54px;
    line-height: 30px;
    margin-bottom: 10px;
  }
  .timer__text{
    font-size: 14px;
    line-height: 30px;
    color: #005BDE;
  }
  .timer__item--center{
    padding-left: 45px;
    padding-right: 45px;
  }
  .timer__item--center .timer__number:after{
    margin-left: 17px;
  }
  .timer__item--center .timer__number:before{
    margin-right: 17px;
  }
  .calendar__pic{
    justify-content: flex-start;
  }
  .caleandar__list{
    flex-wrap: wrap;
  }
  .calendar__item{
    width: calc(50% - 30px);
    margin-bottom: 80px;
    padding-bottom: 34px;
    border-radius: 16px;
    .calendar__panel{
      height: auto;
      padding-top: 58px;
    }
  }
  
} // 980

@media(max-width: 850px){
  .footer-main{
    display: block;
  }
  .footer__right{
    width: 100%;
  }
}

@media(max-width: 768px){
  .main{
    background: url(../img/main__bg-mobile.jpg) no-repeat center / cover;
    padding-bottom: 40px;
  }
  .about__white{
    margin-top: 0px;
  }
  .calendar__pic{
    justify-content: center;
  }
  .calendar__text{
    text-align: center;
    font-weight: 800;
    font-size: 18px;
    line-height: 45px;
    width: 100%;
    padding-bottom: 0px;
    margin-bottom: 0px;
    &:after{
      display: none;
      padding-bottom: 0px;
    }
  }
  .caleandar__list{
    padding-top: 0px;
  }
  .calendar__date{
    text-align: center;
    font-weight: 800;
    font-size: 22px;
    line-height: 30px;
    color: #005BDE;
  }
  .calendar__item{
    width: calc(100% - 30px);
    margin-bottom: 30px;
  }
  .calendar{
    padding-bottom: 0px;
  }
  .organizations__panels{
    flex-direction: column;
    padding-top: 20px;
  }
  .organizations__panel,
  .organizations__panel:first-child,
  .organizations__panel:last-child{
    margin: 0 auto 20px;
  }
  .footer__left{
    margin: 0 auto 50px;
    h2{
      text-align: center;
    }
  }
  .footer__right{
    flex-wrap: wrap;
  }
  .footer__right-end{
    width: 100%;
    text-align: center;
    order: -1;
    .nav{
      display: none;
    }
  }
  .footer-main .footer__lk{
    text-align: center;
  }
  .footer-main{
    .socilas{
      margin: 0 auto;
    }
  }
  .footer__right-start{
    width: 100%;
    padding-top: 0px;
    .political{
      text-align: center;
    }
  }
  .organizations{
    padding-bottom: 50px;
  }
}


@media(max-width: 1250px){
  .footer__wrap {
    flex-wrap: wrap;
  }
  .copyright{
    order: 1;
    width: 50%;
    padding-top: 30px;
    max-width: 100%;
  }
  .political{
    order: 1;
    width: 50%;
    text-align: right;
    padding-top: 26px;
  }
}

@media(max-width: 768px){
  .footer__lk{
    width: 100%;
    order: -1;
    text-align: center;
    margin-bottom: 49px;
    a{
      font-size: 16px;
      line-height: 20px;
    }
  }
  .contacts{
    width: 100%;
    text-align: center;
    margin-bottom: 60px;
  }
  .socilas {
    width: 100%;
    margin-bottom: 50px;
  }
  .social{
    width: 40px;
    height: 40px;
  }
  .political{
    width: 100%;
    text-align: center;
    padding-top: 0px;
    max-width: 100%;
    order: 0;
    margin-bottom: 50px;
  }
  .copyright{
    padding-top: 0px;
    font-size: 14px;
    line-height: 20px;
    color: #6BA8FF;
    text-align: center;
    width: 100%;
    max-width: 360px;
    margin: 0 auto;
  }
}