.winners__form{
  margin-bottom: 90px;
}
.search{
  margin-bottom: 60px;
}

.search__left{
  width: calc(66.6% - 8px);
  position: relative;
  .input{
    margin-bottom: 0px;
  }
}
.search__btn{
  position: absolute;
  height: 100%;
  right: 18px;
  display: flex;
  align-items: center;
  top: 0;
}
.switch__content{
  width: calc(33.33% - 30px);
  margin-bottom: 33px;
  margin-right: 30px;
}

.selects {
  display: flex;
  margin-right: -30px;
  .select{
    width: calc(33.33% - 30px);
    margin-right: 30px;
  }
}

.switch{
  background: #FFFFFF;
  border: 1px solid #005BDE;
  border-radius: 33px;
  width: 56px;
  height: 28px;
  position: relative;
  cursor: pointer;
  margin-right: 30px;
  &.active:after{
    left: 32px;
  }
  &:after{
    transition: .3s;
    content: "";
    position: absolute;
    left: 4px;
    top: 4px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #005BDE;
  }
}

.switch__text{
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
  color: #005BDE;
  text-transform: uppercase;
}

.winners__item{
  display: flex;
  justify-content: space-between;
  padding: 38px 34px 22px;
  background: rgba(166, 221, 250, 0.2);
  border-radius: 6px 14px 14px 6px;
  margin-bottom: 60px;
}

.winners__info{
  width: calc(100% - 400px);
}

.winners__pic{
  width: 320px;
  height: 320px;
  align-self: flex-start;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}

.winners__link{
  display: block;
  margin-bottom: 38px;
  font-weight: bold;
  font-size: 22px;
  line-height: 30px;
  color: #0005A5;
  transition: .4s;
  &:hover{
    background-color: #005BDE;
    text-decoration: underline;
  }
}

.winners__name{
  margin-bottom: 38px;
  font-weight: bold;
  font-size: 30px;
  line-height: 30px;
  color: #161616;
}

.winners__description {
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
}

.winners__ico{
  position: absolute;
  right: 0;
  top: 0;
  background: #005BDE;
  border-radius: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
}

.winners__btn{
  padding-top: 40px;
}


@media(max-width: 1100px){
  .switch__text{
    font-size: 16px; 
  }
}

@media(max-width: 980px){
  .winners__item{
    flex-wrap: wrap;
  }
  .winners__info{
    width: 100%;
  }
  .winners__pic{
    order: -1;
    width: 204px;
    height: 204px;
    margin-bottom: 28px;
  }
  .winners__link{
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 30px;
  }
  .winners__name {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 30px;
  }
  .winners__item{
    padding: 24px;
  }
  .winners__description{
    font-size: 12px;
    line-height: 16px;
  }
  .winners__ico{
    width: 60px;
    height: 60px;
  }
  .svgsprite._win-ico-1{
    width: 24px;
    height: 40px;
  }
  .svgsprite._win-ico-2{
    width: 37px;
    height: 37px;
  }
  .winners__btn{
    padding-top: 0px;
  }
  .switch__content {
    display: none;
  }
  .search__left{
    width: 100%;
  }
  .search{
    margin-bottom: 30px;
  }
  .selects{
    display: block;
    margin-right: 0px;
    .select{
      width: 100%;
    }
  }
  .winners__form{
    padding: 0;
    background-color: transparent;
    margin-bottom: 40px;
  }
  .winners{
    h2{
      text-align: center;
    }
  }
}