.jury__list {
  display: flex;
  margin-right: -20px;
  flex-wrap: wrap;
  justify-content: space-between;
  &:after{
    content: "";
    position: relative;
    height: 1px;
    width: calc(33.3% - 20px);
  }
}

.jury__item{
  width: calc(33.3% - 20px);
  margin-right: 20px;
  margin-bottom: 90px;
  position: relative;
  overflow: hidden;
}

.jury__pic{
  width: 350px;
  height: 350px;
  border-radius: 50%;
  margin: 0 auto 24px;
}


.jury__name{
  text-align: center;
  max-width: 350px;
  margin: 0 auto 16px;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  color: #005BDE;
}

.jury__text{
  padding-top: 22px;
  border-top: 1px solid #D0D2D3;
  text-align: center;
  font-weight: 600;
  max-width: 350px;
  margin: 0 auto;
}

.swiper{
  display: none;
}

.swiper-pagination{
  text-align: left;
}

.swiper-pagination-bullet-active{
  background-color: #005BDE;
}

.jury-lk{
  max-width: 1200px;
  margin: 0 auto;
}

.jury-lk__panel{
  display: flex;
  padding-bottom: 60px;
  margin-bottom: 60px;
  border-bottom: 1px solid #C5C5C5;
}

.jury-lk__right {
  width: 400px;
}

.jury-lk__left{
  width: calc(100% - 438px);
  padding-right: 38px;
}

.jury-lk__link{
  font-weight: bold;
  font-size: 24px;
  line-height: 33px;
  color: #0005A5;
  margin-bottom: 54px;
  display: block;
  padding-top: 20px;
  transition: .3s;
  &:hover{
    color: #2CE77D;
  }
}

.jury-lk__names {
  font-weight: bold;
  font-size: 30px;
  line-height: 30px;
}

.jury-lk__right{
  display: flex;
  justify-content: space-between;
}

.jury-lk__number{
  width: calc(50% - 20px);
}

.jury-lk__number-panel{
  border-radius: 50%;
  width: 100%;
  height: 180px;
  background: #A6DDFA;
  font-weight: 900;
  font-size: 47px;
  line-height: 64px;
  color: #005BDE;
  display: flex;
  justify-content: center;
  align-items: center;
  &.--disabled{
    background: #EBEBEB;
    color: #CE241F;
  }
  &.--lightblue{
    background-color:  rgba(166, 221, 250, 0.2);;
  }
}

.jury-lk__number-text{
  text-align: center;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  color: #000;
}


@media(max-width: 1200px){
  .jury__item{
    width: calc(50% - 53px);
  }
}

@media(max-width: 980px){
  .news-inner{
    display: block;
    .h2{
      font-weight: bold;
      font-size: 18px;
      line-height: 20px;
      margin-bottom: 30px;
      max-width: 600px;
    }
  }
  .news-inner__text{
    max-width: 100%;
  }
  .news-inner__content{
    width: 100%;
  }
  .news-inner__date{
    width: 100%;
    margin-bottom: 3px;
    font-weight: 800;
    font-size: 15px;
    line-height: 20px;
  }
  .jury-lk__left{
    width: 100%;
    padding-right: 0px;
  }
  .jury-lk__panel{
    display: block;
  }
  .jury-lk__link{
    font-weight: 600;
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 20px;
  }
  .jury-lk__names{
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 25px;
  }
  .jury-lk__number-panel{
    height: 140px;
    order: -1;
  }
  .jury-lk__right{
    max-width: 215px;
    width: 100%;
  }
  .jury-lk__number{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .jury-lk__number-text{
    font-weight: 500;
    font-size: 13px;
    line-height: 30px;
  }
  .jury__pic{
    width: 240px;
    height: 240px;
    margin-bottom: 10px;
  }
  .jury__name{
    font-size: 18px;
    line-height: 20px;
    max-width: 230px;
    margin-bottom: 5px;
  }
  .jury__text{
    border: none;
    padding-top: 10px;
    font-size: 14px;
    line-height: 20px;
  }
  .jury-lk__number-panel{
    width: 93px;
    height: 93px;
    font-weight: 800;
    font-size: 30px;
    line-height: 30px;
    background-color: #fff;
  }
  .jury-lk__number-panel.--lightblue{
    background-color: #fff;
  }
  .jury-lk__number{
    width: 93px;
  }
}

@media(max-width: 768px){
  .jury-lk__panel{
    padding: 24px;
    border-radius: 6px;
    background: rgba(166, 221, 250, 0.2);
    border-bottom: none;
    margin-bottom: 40px;
  }
  .jury__list {
    justify-content: center;
  }
  .jury__item{
    width: 240px;
    margin-bottom: 38px;
  }
  
}