
.news{
  .btn{
    width: 100%;
    max-width: 360px;
  }
}
.news__list{
  display: flex;
  flex-wrap: wrap;
  margin-right: -100px;
}

.news__item{
  width: calc(50% - 100px);
  margin-right: 100px;
  margin-bottom: 115px;
}

.news__item--main{
  display: flex;
  justify-content: space-between;
  width: calc(100% - 100px);
  .news__description{
    width: 435px;
  }
  .news__pic{
    width: calc(100% - 465px);
  }
}

.news__pic{
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 50px;
  img{
    width: 100%;
  }
}

.news__date{
  font-weight: bold;
  font-size: 24px;
  line-height: 33px;
  margin-bottom: 5px;
  color: #0005A5;
}

.news__title{
  font-weight: bold;
  font-size: 24px;
  line-height: 33px;
  text-decoration: underline;
  color: #005BDE;
  margin-bottom: 34px;
}

.news__text{
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
}


.news__slider {
  padding-bottom: 55px;
  overflow: hidden;
  .swiper-pagination{
    visibility: hidden;
  }
}
.news-inner{
  display: flex;
  .news__pic{
    margin-bottom: 0px;
  }
}

.news-inner__date{
  width: 230px;
  font-weight: 800;
  font-size: 20px;
  line-height: 27px;  
  color: #A2A6AB;
}

.news-inner__content{
  width: calc(100% - 230px);
}


.news-inner__info{
  display: flex;
  justify-content: space-between;
  .btn{
    height: 80px;
  }
}
.news-inner__right{
  width: calc(100% - 400px);
}
.news-inner__info .news__date{
  height: 120px;
  width: 305px;
  background-color: #EEF8F2;
  font-weight: bold;
  font-size: 26px;
  line-height: 35px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .4s;
}

.news-inner__text{
  margin-bottom: 60px;
  max-width: 750px;
  p{
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 30px;
  }
}



@media(max-width: 980px){
  .news__item--main{
    display: block;
    width: calc(50% - 30px);
    margin-right: 30px;
    .news__pic{
      width: 100%; 
    }
    .news__description{
      width: 100%;
    }
  }
  .news__list{
    margin-right: -30px;
  }
  .news__item{
    width: calc(50% - 30px);
    margin-right: 30px;
  }
  .news__slider{
    .swiper-pagination{
      visibility: visible;
      text-align: center;
    }
  }
  .news-inner__info{
    display: block;
    .btn{
      height: 60px;
    }
    .news__date{
      display: block;
      height: auto;
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      color: #A2A6AB;
      background-color: transparent;
      padding-bottom: 5px;
    }
  }
  .news-inner__right{
    width: 100%;
  }
  .news-inner__text{
    p{
      font-size: 14px;
      line-height: 20px;
    }
  }
}


@media(max-width: 768px){
  .news__item{
    width: calc(100% - 30px);
    margin-right: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid #D0D2D3;
    margin-bottom: 40px;
  }
  .news__pic{
    margin-bottom: 23px;
  }
  .news__date{
    font-weight: 800;
    font-size: 15px;
    line-height: 20px;
    color: #A2A6AB;
    margin-bottom: 3px;
  }
  .news__title{
    font-weight: bold;
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 10px;
  }
  .news__text{
    padding-left: 23px;
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
  }
}