.protection{
  max-width: 1200px;
  margin: 0 auto;
}
.protection__list {
  max-width: 900px;
}
.protection__item{
  margin-bottom: 38px;
}
.protection__info{
  display: flex;
  justify-content: space-between;
}

.protection__left{
  width: 230px;
}

.protection__right{
  width: calc(100% - 230px);
}

.protection__date{
  font-weight: bold;
  font-size: 26px;
  line-height: 35px;
  color: #0005A5;
}
.protection__time{
  font-weight: bold;
  font-size: 26px;
  line-height: 35px;
  color: #005BDE;
}
.protection__text{
  font-size: 20px;
  font-weight: 600;
}
.protection__video{
  margin-bottom: 68px;
  iframe{
    width: 100%;
  }
}
.protection-right{
  display: flex;
  justify-content: flex-end;
}
.protection--timing{
  .protection__left{
    width: 400px;
    align-self: flex-start;
    height: 120px;
    display: flex;
    align-items: center;
    padding-left: 86px;
    position: relative;
    flex-wrap: wrap;
    &:after{
      content: "";
      position: absolute;
      left: 0;
      width: 12px;
      height: 100%;
      border-radius: 6px 0 0 6px;
      background: linear-gradient(180deg, #0005A5 0%, #00ACFF 100%);
    }
  }
  .protection__right{
    width: calc(100% - 395px);
    border-bottom: 1px solid #C5C5C5;
    padding-top: 30px;
    &.--noborder{
      border: none;
    }
  }
  .protection__text{
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
  }
  .protection__date,
  .protection__time{
    font-size: 26px;
    width: 100%;
  }
}

@media(max-width: 980px){
  .protection--timing .protection__info{
    display: block;
  }
  .protection--timing .protection__right{
    width: 100%;
    border: none;
    padding-top: 15px;
    padding-bottom: 0px;
  }
  .protection--timing .protection__date,
  .protection--timing .protection__time{
    font-size: 18px;
    line-height: 30px;
  }
  .protection__date, .protection__time{
    font-size: 13px;
    line-height: 20px;
  }
  .protection--timing .protection__date, .protection--timing .protection__time{
    font-size: 18px;
    line-height: 30px;
  }
  .protection__left{
    width: 110px;
  }
  .protection__right{
    width: calc(100% - 110px);
  }
  .protection--timing .protection__right{
    width: 100%;
  }
  .protection--timing .protection__left{
    padding-left: 20px;
    margin-left: -20px;
    height: auto;
    width: 100%;
    &:after{
      border-radius: 0;
    }
  }
  .protection--timing .protection__text{
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
  }
  .protection__text{
    font-size: 13px;
  }
  .protection__item{
    margin-bottom: 45px;
  }
}

@media(max-width: 600px){
  .protection__video{
    margin-bottom: 20px;
  }
  
  .protection .h2{
    text-align: center;
  }
  .inner{
    padding-top: 67px;
  }
}